import React, { useEffect, useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../../context/auth";

const Signup = () => {
  const [roles, setRoles] = useState(null);
  const [name, setName] = useState(null);
  const [email, setEmail] = useState(null);
  const [password, setPassword] = useState(null);
  const [confirmPassword, setConfirmPassword] = useState(null);
  const [role, setRole] = useState(null);
  const navigate = useNavigate();

  const [auth, setAuth] = useAuth();

  const getRoles = async () => {
    try {
      const res = await axios.get(`/api/v1/basic/roles`);
      if (res.data.success) {
        setRoles(res.data.roles);
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getRoles();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();

    const user = {
      name,
      email,
      password,
      confirmPassword,
      role,
    };
    try {
      const response = await axios.post(`/api/v1/auth/signup`, user);
      if (response.status === 200) {
        setAuth({
          ...auth,
          user: response.data.user,
          token: response.data.token,
        });
        localStorage.removeItem("auth");
        localStorage.setItem("auth", JSON.stringify(response.data));
        navigate(`/${role}/configure`);
      } else {
        alert(response?.data?.message);
      }
    } catch (error) {
      alert(error?.response?.data?.message);
    }
  };

  return (
    <div className="register-page">
      <div className="register-box">
        <div className="register-logo">
          <a>
            <b>Create </b> a new account
          </a>
        </div>
        <div className="card">
          <div className="card-body register-card-body">
            {role && <p className="login-box-msg">Register as {role}</p>}
            <form onSubmit={handleSubmit}>
              <div className="input-group mb-3">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Full name"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
                <div className="input-group-append">
                  <div className="input-group-text">
                    <span className="fas fa-user" />
                  </div>
                </div>
              </div>
              <div className="input-group mb-3">
                <input
                  type="email"
                  className="form-control"
                  placeholder="Email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
                <div className="input-group-append">
                  <div className="input-group-text">
                    <span className="fas fa-envelope" />
                  </div>
                </div>
              </div>
              <div className="input-group mb-3">
                <input
                  type="password"
                  className="form-control"
                  placeholder="Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
                <div className="input-group-append">
                  <div className="input-group-text">
                    <span className="fas fa-lock" />
                  </div>
                </div>
              </div>
              <div className="input-group mb-3">
                <input
                  type="password"
                  className="form-control"
                  placeholder="Retype password"
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                />
                <div className="input-group-append">
                  <div className="input-group-text">
                    <span className="fas fa-lock" />
                  </div>
                </div>
              </div>
              <div className="input-group mb-3">
                <select
                  className="form-control select2bs4 select2-hidden-accessible"
                  aria-hidden="true"
                  value={role}
                  onChange={(e) => setRole(e.target.value)}
                >
                  <option selected disabled>
                    Select role
                  </option>
                  {roles &&
                    roles.map((role) => <option value={role}>{role}</option>)}
                </select>
                <div className="input-group-append">
                  <div className="input-group-text">
                    <span className="fas fa-lock" />
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-8">
                  <div className="icheck-primary">
                    <input
                      type="checkbox"
                      id="agreeTerms"
                      name="terms"
                      defaultValue="agree"
                    />
                    <label htmlFor="agreeTerms">
                      I agree to the <a href="#">terms</a>
                    </label>
                  </div>
                </div>
                {/* /.col */}
                <div className="col-4">
                  <button type="submit" className="btn btn-primary btn-block">
                    Register
                  </button>
                </div>
                {/* /.col */}
              </div>
            </form>

            <a onClick={() => navigate("/signin")} className="text-center">
              I already have a membership
            </a>
          </div>
          {/* /.form-box */}
        </div>
        {/* /.card */}
      </div>
    </div>
  );
};

export default Signup;
