import { Routes, Route } from "react-router-dom";
import AdminRouters from "./routers/AdminRouters";
import Signup from "./pages/Signup";
import ConfigureAdmin from "./pages/Configure/ConfigureAdmin";
import AdminRoute from "./routes/AdminRoute";
import AdminDashboard from "./pages/Dashboard/AdminDashboard";
import Signin from "./pages/Signin";
import ConfigureApprover from "./pages/Configure/ConfigureApprover";
import ApproverRoute from "./routes/ApproverRoute";
import ApproverDashboard from "./pages/Dashboard/ApproverDashboard";

function App() {
  return (
    <>
      <Routes>
        {/* <Route path="/admin" element={<AdminRouters />} /> */}
        <Route path="/signup" Component={Signup} />
        <Route path="/signin" Component={Signin} />

        <Route path="/admin" Component={AdminRoute}>
          <Route path="configure" Component={ConfigureAdmin} />
          <Route path="dashboard" Component={AdminDashboard} />
        </Route>

        <Route path="/approver" Component={ApproverRoute}>
          <Route path="configure" Component={ConfigureApprover} />
          <Route path="dashboard" Component={ApproverDashboard} />
        </Route>
      </Routes>
    </>
  );
}

export default App;
