import React, { useEffect, useState } from "react";
import { useAuth } from "../../context/auth";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const ConfigureAdmin = () => {
  const [auth, setAuth] = useAuth();
  const [levelName, setLevelName] = useState("");
  const [levels, setLevels] = useState([]);
  const [institude, setInstitude] = useState("");
  const [show, setShow] = useState();
  const navigate = useNavigate();

  const capitalize = (str) => {
    const resultStr = str
      .trim()
      .split(" ")
      .map(
        (word) => word.trim().charAt(0).toUpperCase() + word.trim().slice(1)
      );
    console.log(resultStr);
    return resultStr.join(" ");
  };
  const addLevels = () => {
    if (levelName.trim() == "") return;
    const existingLevel = levels.find(
      (level) => level.name == capitalize(levelName)
    );
    if (!existingLevel) {
      const newLevel = {
        name: capitalize(levelName),
      };
      setLevels([...levels, newLevel]);
      setLevelName("");
    }
    setLevelName("");
  };

  const removeLevel = (name) => {
    setLevels(levels.filter((level) => level.name !== name));
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!institude || !institude === "") {
      alert("You need to enter institude name");
      return;
    }
    if (!levels.length > 0) {
      alert("You need to add atleast one level");
      return;
    }

    const config = {
      institute_name: institude,
      institute_type: "manager",
      admin: auth.user._id,
      levels: levels,
    };

    try {
      const response = await axios.post(`/api/v1/config/admin`, config, {
        headers: {
          Authorization: `${auth?.token}`,
        },
      });
      if (response.status == 200) {
        navigate("/admin/dashboard");
      } else {
        alert(response.data.message);
      }
    } catch (err) {
      alert(err?.response?.data?.message);
    }
  };

  useEffect(() => {
    const authCheck = async () => {
      try {
        const response = await axios.get(`/api/v1/users/${auth?.user?._id}`);
        if (response.data) {
          if (response?.data?.user?.status === "active") {
            setShow(false);
            navigate("/admin/dashboard");
          } else if (response?.data?.user?.status !== "verified") {
            alert("Please wait untill we verify your account");
            setShow(false);
            navigate("/");
          } else {
            setShow(true);
          }
        } else {
          setShow(false);
        }
      } catch (error) {
        setShow(false);
      }
    };
    if (auth?.user) {
      authCheck();
    } else {
      navigate("/signin");
    }
  }, []);

  return (
    auth &&
    show && (
      <div className=" register-page">
        <div className="register-box">
          <div className="register-logo">
            <a>
              <b>Configure </b> Admin
            </a>
          </div>
          <div className="card">
            <div className="card-body register-card-body">
              <p className="login-box-msg">Hello {auth?.user?.name}</p>
              <form onSubmit={handleSubmit}>
                <div className="input-group mb-3">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Name of your Institute"
                    value={institude}
                    onChange={(e) => setInstitude(e.target.value)}
                  />
                  <div className="input-group-append">
                    <div className="input-group-text">
                      <span className="fas fa-regular fa-building" />
                    </div>
                  </div>
                </div>

                <div className="input-group mb-3">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Add a level"
                    value={levelName}
                    onChange={(e) => setLevelName(e.target.value)}
                  />
                  <div className="input-group-append" onClick={addLevels}>
                    <div className="btn btn-success">
                      <span className="fas fa-plus" />
                    </div>
                  </div>
                </div>
                <div class="alert alert-danger">
                  <h5>
                    <i className="icon fas fa-regular fa-user"></i>
                    Admin
                  </h5>
                </div>
                {levels &&
                  levels.map((level) => (
                    <div class="alert alert-info">
                      <button
                        type="button"
                        class="close"
                        onClick={() => removeLevel(level.name)}
                      >
                        ×
                      </button>
                      <h5>
                        <i className="icon fas fa-arrow-up"></i>
                        {level.name}
                      </h5>
                    </div>
                  ))}

                <div className="row">
                  <div className="col-8">
                    <div className="icheck-primary">
                      <input
                        type="checkbox"
                        id="agreeTerms"
                        name="terms"
                        defaultValue="agree"
                      />
                      <label htmlFor="agreeTerms">
                        I agree to the <a href="#">terms</a>
                      </label>
                    </div>
                  </div>
                  {/* /.col */}
                  <div className="col-4">
                    <button type="submit" className="btn btn-primary btn-block">
                      Configure
                    </button>
                  </div>
                  {/* /.col */}
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    )
  );
};

export default ConfigureAdmin;
